#!/usr/bin/env node
/**
 * /zest:status command handler
 *
 * Displays authentication and sync status
 */

import { loadSession } from "../auth/session-manager.js";
import { loadSettings } from "../config/settings.js";
import { getDaemonPid } from "../utils/daemon-manager.js";
import { logger } from "../utils/logger.js";

async function main() {
  try {
    // Load all status data
    const session = await loadSession();
    const daemonPid = await getDaemonPid();
    const settings = await loadSettings();

    if (session) {
      // Authenticated
      console.log(` Authenticated: ${session.email}`);

      // Show workspace
      if (session.workspaceId && session.workspaceName) {
        console.log(` Workspace: ${session.workspaceName}`);
      } else {
        console.log("  No workspace selected - run /zest:workspace");
      }

      // Show daemon status
      if (daemonPid) {
        console.log(` Sync daemon: Running (PID: ${daemonPid})`);
      } else {
        console.log("  Sync daemon: Not running (will auto-start on next session)");
      }

      // Show remote persistence status
      if (settings.enableRemotePersistence) {
        console.log(" Remote sync: Enabled");
      } else {
        console.log(" Remote sync: Disabled (local only)");
      }
    } else {
      // Not authenticated
      console.log(" Not authenticated (tracking locally)");

      // Show daemon status
      if (daemonPid) {
        console.log(` Sync daemon: Running (PID: ${daemonPid})`);
      } else {
        console.log(" Sync daemon: Not running");
      }

      // Show remote persistence status
      if (settings.enableRemotePersistence) {
        console.log(" Remote sync: Enabled (waiting for auth)");
      } else {
        console.log(" Remote sync: Disabled (local only)");
      }
    }
  } catch (error) {
    logger.error("Failed to get status", error);
    console.error(" Failed to get status");
    if (error instanceof Error) {
      console.error(`   ${error.message}`);
    }
    process.exit(1);
  }
}

main();
